
	






import { mapMutations } from 'vuex';
import utils from './common/util';
const { safeGet, getLocationUrl } = utils;
export default {
	data() {
		return {
			bgAudioMannager: null
		};
	},
	methods: { ...mapMutations(['setCity']) },
	onLaunch: function() {
		let that = this;
		// uni.setStorageSync("UList",[]);
		console.log('App Launch');




		uni.getLocation({
			type: 'wgs84',
			success: function(res) {
				const { latitude, longitude } = res;
				uni.request({
					url: getLocationUrl(latitude, longitude),
					success: location => {
						const { city, adcode, district } = safeGet(['data', 'result', 'ad_info'], location);
						console.log(city, adcode);
						uni.getStorage({
							key: 'city',
							success: function() {},
							fail: function() {
								console.log('第一次定位');
								that.setCity(city);
								uni.setStorageSync('city', city);
							}
						});
					}
				});
			},
			fail: function() {
				uni.showToast({
					title: '定位失败',
					icon: 'none'
				});
			}
		});
	},
	onShow: function() {
		console.log('App Show');



	},
	onHide: function() {
		console.log('App Hide');






	}
};
